.quote__text {
  font-family: 'robotolight_italic', sans-serif;
  padding: 0;
  margin: 0;
  opacity: 0.5; }

.quote__author {
  padding: 0;
  font-family: 'robotobold_italic', sans-serif; }

.review {
  display: flex;
  flex-direction: column;
  align-items: center; }

.review__author {
  font-style: italic;
  color: #4e8839;
  font-weight: bold; }

.section {
  min-height: 100%;
  width: 100%;
  background-color: #faf8f0; }

.section__title {
  font-family: 'sansus_webissimoregular', sans-serif;
  font-size: 63px;
  color: #fff;
  padding: 0 0 30px 0;
  margin: 0;
  max-width: 380px;
  text-transform: uppercase;
  position: relative;
  display: block; }
  .section__title::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -45px;
    background-color: #fff;
    width: 90px;
    height: 3px; }
  .section__title--dark {
    color: #373e42; }
    .section__title--dark::after {
      background-color: #373e42; }

.section__subtitle {
  font-family: 'sansus_webissimoregular', sans-serif;
  font-size: 35px;
  color: #566358;
  padding: 0 0 30px 0;
  margin: 0 auto;
  text-transform: uppercase;
  position: relative;
  text-align: center;
  max-width: 295px; }
  .section__subtitle::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -45px;
    background-color: #566358;
    width: 90px;
    height: 3px; }
  .section__subtitle--white {
    color: #fff; }
    .section__subtitle--white::after {
      background-color: #fff; }

.section__backing {
  position: absolute;
  opacity: 0.2;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%); }
  .section__backing--header {
    height: 50%;
    width: 80%; }
  .section__backing--subtitle {
    top: 25%;
    width: 100%; }

.section__title-box {
  position: relative; }

.skill {
  position: relative;
  display: inline-block;
  width: 150px;
  height: 150px; }

.skill__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
  color: #1bb696;
  font-weight: bold; }

.skills__title {
  font-family: 'robotomedium', sans-serif;
  font-size: 28px;
  color: #566358;
  margin: 0;
  padding: 0; }

.skills__row {
  display: flex;
  flex-wrap: wrap; }

.slider {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #ebefe2; }
  @media screen and (max-width: 768px) {
    .slider {
      flex-wrap: nowrap; } }

.slider__info {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.slider__display {
  width: 50%;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media screen and (max-width: 768px) {
    .slider__display {
      order: -10; } }

.slider__btns {
  width: 50%;
  height: 30%;
  display: flex; }

@media screen and (max-width: 768px) {
  .slider__info, .slider__display, .slider__btns {
    width: 100%; } }

.slider__title {
  margin-bottom: 25px; }

.slider__desc {
  margin-bottom: 25px;
  font-family: 'robotomedium', sans-serif;
  color: #4e8839; }

.slider__work {
  width: 80%;
  height: auto;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }

.slider__left, .slider__right {
  height: 100%;
  width: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center; }

.slider__btn {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #9ea29a, #b4b9b0);
  opacity: 0.5; }

.slider__arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 48px;
  height: 31px;
  margin-left: -24px; }

.socials {
  display: flex;
  align-items: center;
  height: 40px; }

.socials__item {
  display: inline-block;
  margin-left: 20px; }
  .socials__item:first-child {
    margin-left: 0; }

.socials__link {
  display: block;
  opacity: 0.5; }

.socials__link:hover {
  opacity: 1; }
  .socials__link:hover .socials__svg {
    fill: #4e8839; }

.speaking {
  background-image: url("../img/works-bg.png");
  background-size: cover;
  background-position: center bottom;
  display: flex;
  flex-direction: column;
  align-items: center; }

.speaking__testimonials {
  margin: 20px auto 50px; }

.speaking__contact-me {
  margin-bottom: 70px; }

.testimonials {
  display: flex;
  width: 60%; }

.testimonials__left {
  width: 50%; }

.testimonials__right {
  width: 50%; }

.textarea {
  background-color: #fff;
  padding: 15px;
  width: 100%;
  min-height: 140px;
  border: none;
  border-radius: 30px;
  color: #455a64; }

.triangles {
  width: 100%;
  margin: 0;
  height: auto;
  margin-top: -8%;
  z-index: 2;
  position: relative; }
  .triangles--sameColor .traingle .triangle__left {
    fill: #faf8f0; }
  .triangles--sameColor .traingle .triangle__right {
    fill: #faf8f0; }

.traingle {
  display: block; }
  .traingle .triangle__left {
    fill: #ebefe2; }
  .traingle .triangle__right {
    fill: #f4f5f0; }

.user__pic {
  width: 100%;
  height: auto; }

.welcome-btn {
  display: inline-block;
  padding: 15px 25px;
  border: 1px solid #fff;
  border-radius: 30px;
  color: #fff; }
  .welcome-btn:hover {
    background-color: #4e8839;
    border: 1px solid transparent; }

.welcome {
  background: url("../img/welcome-bg.jpg") center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative; }

.welcome__inner {
  width: 380px;
  min-height: 395px;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  overflow: hidden; }

.welcome__button {
  position: absolute;
  top: 20px;
  right: 80px; }

.welcome__copyright {
  position: absolute;
  bottom: 45px; }

.works__main {
  width: 100%;
  height: 100vh;
  margin-bottom: 20%; }

.works__title {
  display: block;
  text-align: center;
  margin: 20px auto; }

.works__content {
  height: 100%; }

.works__about {
  width: 100%;
  min-height: 150vh;
  position: relative;
  display: flex;
  flex-direction: column; }

.works__footer {
  width: 100%;
  height: 185px;
  margin-top: -182px;
  z-index: 2;
  position: absolute; }
